import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { GroupsService } from '../groups.service';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-group',
  templateUrl: './edit-group.component.html',
  styleUrls: ['./edit-group.component.scss'],
})
export class EditGroupComponent implements OnInit, AfterViewInit {
  image;
  groupId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private groupsService: GroupsService,
    private toastr: ToastrService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
    name: ['', Validators.required],
  });

  ngOnInit(): void {
    this.groupId = this.route.snapshot.paramMap.get('id');
    this.getOne(this.groupId);
  }
  ngAfterViewInit() {}

  getOne(id) {
    this.groupsService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];
        this.form.get('image').patchValue(res['data']['image']);
        this.form.get('name').patchValue(res['data']['name']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }

  onSubmit(formValue) {
    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.groupsService.updateOne(formData, this.groupId).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['../']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );
  }
}
